
import React from "react";
import trainingData from "../data/training-data.json";

const Visualization = () => {
  return (
    <section className="p-8">
      <h2 className="text-2xl font-bold mb-4">Model Performance</h2>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {trainingData.map((model, index) => (
          <div key={index} className="border rounded p-4 shadow-md">
            <h3 className="text-lg font-bold">{model.model}</h3>
            <p>Accuracy: {model.accuracy}%</p>
            <p>Training Loss: {model.trainingLoss}</p>
            <p>Validation Loss: {model.validationLoss}</p>
          </div>
        ))}
      </div>
    </section>
  );
};

export default Visualization;
